<template>
    <b-container id="app" v-resize:debounce="resizeHandler" fluid class="is-container">
        <b-row no-gutters>
            <b-col col>
                <b-navbar class="is-navbar" toggleable="lg" type="dark" variant="dark">
                    <b-navbar-brand href="#"> i² Clustering</b-navbar-brand>
                    <b-navbar-toggle target="nav_collapse" />
                    <b-collapse is-nav id="nav_collapse">
                        <b-navbar-nav class="ml-auto">
                            <b-nav-item-dropdown right>
                                <!-- Using button-content slot -->
                                <template slot="button-content">
                                    <font-awesome-icon :icon="['fas', 'user-circle']" size="lg"/>&nbsp; User
                                </template>
                                <b-dropdown-item href="#">
                                    <font-awesome-icon :icon="['fas', 'sign-out-alt']"/>&nbsp; Signout
                                </b-dropdown-item>
                            </b-nav-item-dropdown>
                        </b-navbar-nav>
                    </b-collapse>
                </b-navbar>
            </b-col>
        </b-row>
        <b-row no-gutters class="wrapper">
            <b-col cols="2" md="auto">
                <b-nav vertical pills id="sidebar" type="dark">
                    <b-nav-item v-for="(item, index) in sidebarItems"
                        :to="item.href"
                        :active="index === activeLiIndex"
                        :index="index"
                        v-on:click="toggleRowActive(index)">
                        <font-awesome-icon :icon="['fas', item.icon]"/>&nbsp; {{ item.name }}
                    </b-nav-item>
                </b-nav>
            </b-col>
            <b-col class="is-main-content" ref="MainContent">
                <keep-alive>
                    <router-view
                        :width="mainContentWidth"
                        :height="mainContentHeight"/>
                </keep-alive>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
export default {
    name: 'App',
    data: function() {
        return {
            sidebarItems: [
                {
                    name: "Dashboard",
                    icon: "tachometer-alt",
                    href: "/dashboard"
                },
                {
                    name: "Document Manager",
                    icon: "list-alt",
                    href: "/documents"
                },
                {
                    name: "Version Control",
                    icon: "project-diagram",
                    href: "/vcs"
                }
            ],
            sidebarCollapsed: false,
            activeLiIndex: 0,
            mainContentWidth: Number,
            mainContentHeight: Number
        }
    },
    mounted: function() {
        this.setViewport()
    },
    methods: {
        toggleSideBar: function (event) {
            this.sidebarCollapsed != this.sidebarCollapsed
        },
        toggleRowActive(index) {
            this.activeLiIndex = index
        },
        setViewport () {
            this.mainContentWidth = this.$refs.MainContent.clientWidth
            this.mainContentHeight = this.$refs.MainContent.clientHeight
        },
        resizeHandler: function () {
            this.setViewport()
        }
    }
}
</script>

<style lang="sass">
@import '../node_modules/bootstrap/scss/bootstrap'
@import '../node_modules/bootstrap-vue/src/index.scss'

$nav-height: 3.25em
$bg-color-primary: #343a40
$bg-color-secondary: #2e3439

html, body
    height: 100% !important
    width: 100% !important
    overflow: hidden

#app
    font-family: 'Avenir', Helvetica, Arial, sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    text-align: center
    color: #2c3e50
    width: 100% !important
    height: 100% !important

.wrapper
    padding: 0
    // align-items: stretch
    height: 100% !important
    // flex-direction: row
    .row
        height: 100%

.is-navbar
    width: 100% !important
    height: $nav-height

.is-container
    width: 100% !important
    height: 100% !important
    padding: 0

.is-main-content
    // margin: auto
    width: 100%
    padding: 0
    text-align: center

#sidebar
    background-color: $bg-color-secondary
    text-align: left
    height: 100%
    padding: 10px 0 10px 10px !important
    li
        padding-right: 10px
        a
            color: #FFF

</style>



