<template ref="MainContent">
  <Promised :promise="dataPromise" tag="b-row" :no-gutters="true" class="wrap">
    <template v-slot:pending>
      <b-col col id="Spinner">
        <b-spinner variant="primary" label="Spinning"></b-spinner>
      </b-col>
    </template>
    <template v-slot="data">
      <b-col col class="is-main-content" ref="SankeySVG" v-resize:debounce="resizeHandler">
        <b-row no-gutters id="sankeyToolbar">
          <b-col class="sankeyToolbar_start">
            <b-button
              variant="dark"
              :pressed="isSorted"
              title="If enabled, will sort the clusters by its id in each session."
            >
              <font-awesome-icon :icon="['fas', 'sort-amount-down']"/>
            </b-button>
          </b-col>
          <b-col class="sankeyToolbar_end">
            <b-button
              variant="dark"
              title="Recalculate the sankey Diagram."
              v-on:click="resizeHandler"
            >
              <font-awesome-icon :icon="['fas', 'sync-alt']"/>
            </b-button>
            <b-button
              variant="dark"
              :pressed="isFullscreen"
              title="Toggle Fullscreen"
              v-on:click="toggleFullscreen"
            >
              <font-awesome-icon :icon="['fas', isFullscreen ? 'compress' : 'expand']"/>
            </b-button>
          </b-col>
        </b-row>
        <b-row no-gutters>
          <svg id="sankey" v-on:click.capture="clearSelection"></svg>
        </b-row>
      </b-col>
      <b-col sm="3" class="rightPanel">
        <b-row no-gutters id="listHeader">
          <b-col col id="listHeaderContent">
            <b-row id="title">
              <span>{{ rightPanel.title }}</span>
            </b-row>
            <b-row id="counter">
              <span>{{ rightPanel.counter }}</span>
            </b-row>
          </b-col>
          <b-col col lg="2" id="clear" :disabled="true">
            <font-awesome-icon v-on:click="clearSelection" :icon="['fas', 'times']" size="lg"/>
          </b-col>
        </b-row>
        <b-row>
          <b-col col>
            <div
              class="docs"
              tag="div"
              v-bind:style="{ 'max-height': height * 0.8 + 'px', 'min-height':  height * 0.8 + 'px'}"
            >
              <document-component
                v-for="(doc, index) in data.data.documents"
                :key="index"
                :doc="doc"
                :colors="getColors(doc.sessions)"
                @hover-document="hoverDocument(doc)"
                @hover-document-out="hoverDocumentOut()"
                @add-document="addDocument"
              ></document-component>
            </div>
          </b-col>
        </b-row>
      </b-col>
    </template>
    <template v-slot:rejected="error">
      <b-col col>
        <p>Error: {{ error.message }}</p>
      </b-col>
    </template>
  </Promised>
</template>

<script>
import axios from "axios";

import * as d3 from "d3";
import * as sankey from "d3-sankey";

if (!String.prototype.format) {
  String.prototype.format = function() {
    var args = arguments;
    return this.replace(/{(\d+)}/g, function(match, number) {
      return typeof args[number] != "null" ? args[number] : match;
    });
  };
}

export default {
  name: "SankeyGraph",
  props: ["width", "height"],
  components: {
    "document-component": {
      name: "document-component",
      props: ["doc", "colors"],
      data: function() {
        return {
          hidden: false
        };
      },
      template: `
                <div v-bind:class="[{'hidden': hidden}, {'doc': true}]"
                    v-on:mouseover="hover"
                    v-on:mouseleave="hoverOut">
                    <font-awesome-icon v-for="(session, i) in doc.sessions"
                        :icon="[checkSession(session), 'circle']"
                        :style="colors[i] !== undefined ? { 'color': colors[i] } : ''"
                        size="xs"/>
                    &nbsp; {{ doc.name }}
                </div>
            `,
      mounted: function() {
        // d3.select(this.$el).data(this)
        this.$emit("add-document", this);
      },
      methods: {
        checkSession: function(x) {
          if (x === undefined) {
            return "far";
          }
          return "fas";
        },
        hover: function() {
          this.$emit("hover-document");
        },
        hoverOut: function() {
          this.$emit("hover-document-out");
        }
      }
    }
  },
  data: function() {
    return {
      id: "sankey",
      svg: null,
      graphSize: null,
      sankeyGraph: null,
      graph: null,
      svgLinks: null,
      svgNodes: null,
      svgSessions: null,
      gradients: null,
      nodeDepths: null,
      selectedNode: undefined,
      selectedLink: undefined,
      selectedSession: undefined,
      data: null,
      dataPromise: null,
      documents: [],
      isFullscreen: false,
      isSorted: true,
      settings: {
        width: 0,
        height: 0,
        margin: 50,
        svgBackground: "#eee",
        svgBorder: "1px solid #333",
        nodeWidth: 10,
        nodePadding: 10,
        nodeOpacity: 0.8,
        linkOpacity: 0.5,
        nodeDarkenFactor: 0.3,
        nodeStrokeWidth: 4,
        arrow: "\u2192",
        nodeAlignment: sankey.sankeyLeft,
        colorScale: d3.interpolateRainbow,
        path: sankey.sankeyLinkHorizontal(),
        nodeSelection: false,
        linkSelection: false,
        sessionSelection: false,
        opacity: {
          faded: 0.1,
          active: 0.8,
          active_link: 0.6,
          selected_link: 0.8,
          selected: 1.0
        }
      },
      rightPanel: {
        title: "",
        counter: ""
      },
      templates: {
        title_default: "All Documents",
        counter: "{0} Document{1}",
        hover_link: `{0} \u2192 {1}\n{2} Document(s)`,
        hover_node: `Cluster {0}\n{1} Document(s)`,
        hover_session: `Session {0}\n{1} Document(s)\nDate: {2}`,
        nodeSelected: `Cluster {0}`,
        linkSelected: `Cluster {0} \u2192 Cluster {1}`,
        sessionSelected: `Session {0}`
      }
    };
  },
  watch: {
    selectedNode: function(node) {
      let title = String;
      let counter = String;

      if (node === undefined) {
        title = this.templates.title_default;
        counter = this.templates.counter.format(this.documents.length, "s");
      } else {
        title = this.templates.nodeSelected.format(node.datum().id);
        counter = this.templates.counter.format(
          node.datum().value,
          node.datum().value > 1 ? "s" : ""
        );
      }

      this.rightPanel = {
        title: title,
        counter: counter
      };
    },
    selectedLink: function(link) {
      let title = String;
      let counter = String;

      if (link === undefined) {
        title = this.templates.title_default;
        counter = this.templates.counter.format(this.documents.length, "s");
      } else {
        let d = link.datum();
        title = this.templates.linkSelected.format(d.source.id, d.target.id);
        counter = this.templates.counter.format(
          link.datum().value,
          link.datum().value > 1 ? "s" : ""
        );
      }

      this.rightPanel = {
        title: title,
        counter: counter
      };
    },
    selectedSession: function(session) {
      let title = String;
      let counter = String;

      if (session === undefined) {
        title = this.templates.title_default;
        counter = this.templates.counter.format(this.documents.length, "s");
      } else {
        title = this.templates.sessionSelected.format(session.name);
        counter = this.templates.counter.format(
          session.number,
          session.number > 1 ? "s" : ""
        );
      }

      this.rightPanel = {
        title: title,
        counter: counter
      };
    }
  },
  created: function() {
    let objRef = this;

    this.dataPromise = axios.post("http://localhost/IC3/cgi-bin/sankey.py", {
      userDirectiory: "../users/eric"
    });
    // this.dataPromise = axios.post("http://localhost:5000/get_data");
  },
  mounted: function() {
    let objRef = this;

    this.dataPromise.then(result => {
      objRef.data = result.data;
      objRef.rightPanel.title = objRef.templates.title_default;
      objRef.rightPanel.counter = objRef.templates.counter.format(
        objRef.data.documents.length,
        "s"
      );
      objRef.$nextTick(function() {
        objRef.calculateSankey();
      });
    });
  },
  methods: {
    toggleFullscreen: function() {
      this.$refs.Fullscreen.toggle();
    },
    fullscreenChange(fullscreen) {
      this.isFullscreen = fullscreen;
    },
    noSelection: function() {
      return (
        !this.settings.nodeSelection &&
        !this.settings.linkSelection &&
        !this.settings.sessionSelection
      );
    },
    addDocument: function(doc) {
      this.documents.push(doc);
    },
    sortNode: function(node1, node2) {
      return node1.id < node2.id ? -1 : 1;
    },
    calculateSankey: function() {
      let objRef = this;

      this.settings.width = this.$refs.SankeySVG.clientWidth;
      this.settings.height = this.$refs.SankeySVG.clientHeight;

      this.svg = d3
        .select("#{0}".format(this.id))
        .append("g")
        .attr(
          "transform",
          "translate({0}, {1})".format(
            this.settings.width / 4,
            this.settings.height / 6
          )
        );

      this.graphSize = [this.settings.width / 2, this.settings.height / 2];

      this.sankeyGraph = sankey
        .sankey()
        .size(this.graphSize)
        .nodeId(d => d.id)
        .nodeWidth(this.settings.nodeWidth)
        .nodePadding(this.settings.nodePadding)
        .nodeAlign(this.settings.nodeAlignment)
        .nodeSort(this.isSorted ? this.sortNode : null); // ADD OPTION TO SWITCH?

      this.graph = this.sankeyGraph(this.data.sankey);

      this.graph.nodes.forEach(node => {
        let fillColor = this.color(node.id);
        node.fillColor = fillColor;
        node.strokeColor = this.darkenColor(
          fillColor,
          this.settings.nodeDarkenFactor
        );
        node.width = node.x1 - node.x0;
        node.height = node.y1 - node.y0;
      });

      let sessionsObject = Object.values(
        JSON.parse(JSON.stringify(this.data.sessions))
      );

      this.svgSesisons = this.svg
        .append("g")
        .classed("sessions", true)
        .selectAll("g")
        .data(sessionsObject)
        .enter()
        .append("g");

      this.svgSesisons
        .classed("session", true)
        .attr("transform", function(d) {
          return "translate({0},{1})".format(
            (objRef.settings.width / 2 / (sessionsObject.length - 1) - 5) *
              d.id,
            0
          );
        })
        .append("text")
        .classed("sessionLabel", true)
        .attr("transform", "translate(5,-20)")
        .attr("fill", "#9a9a9a")
        .style("font-size", "18px")
        .style("text-anchor", "middle")
        .style("cursor", "pointer")
        .text(d => d.name)
        .on("click", d => {
          event.stopPropagation();
          objRef.selectSession(d);
        });

      this.svgSesisons
        .append("title")
        .text(d =>
          this.templates.hover_session.format(d.name, d.number, d.date)
        );

      this.svgLinks = this.svg
        .append("g")
        .classed("links", true)
        .selectAll("g")
        .data(this.graph.links)
        .enter()
        .append("g");

      this.gradients = this.svgLinks
        .append("linearGradient")
        .attr("gradientUnits", "userSpaceOnUse")
        .attr("x1", d => d.source.x1)
        .attr("x2", d => d.target.x0)
        .attr("id", d => this.getGradientId(d));

      this.addGradientStop(this.gradients, 0.0, d => this.color(d.source.id));
      this.addGradientStop(this.gradients, 1.0, d => this.color(d.target.id));

      this.svgLinks
        .append("path")
        .classed("link", true)
        .attr("d", this.settings.path)
        .attr("fill", "none")
        .attr("stroke", d => `url(#${this.getGradientId(d)})`)
        .attr("stroke-width", d => Math.max(1.0, d.width))

        .on("mouseover", function(d) {
          objRef.hoverLink(d3.select(this));
        })
        .on("mouseout", function(d) {
          objRef.hoverLinkOut(d3.select(this));
        })
        .on("click", function() {
          event.stopPropagation();
          objRef.selectLink(d3.select(this));
        });

      this.svgLinks
        .select("path")
        .style("-webkit-transition", "opacity .5s")
        .style("opacity", this.settings.opacity.active_link);

      // Add hover effect to links.
      this.svgLinks
        .append("title")
        .text(d =>
          this.templates.hover_link.format(d.source.id, d.target.id, d.value)
        );

      this.svgNodes = this.svg
        .append("g")
        .classed("nodes", true)
        .classed("faded", false)
        .selectAll("rect")
        .data(this.graph.nodes)
        .enter()
        .append("rect")
        .classed("node", true)
        .style("-webkit-transition", "opacity .5s")
        .style("opacity", this.settings.opacity.active)
        .attr("x", d => d.x0)
        .attr("y", d => d.y0)
        .attr("width", d => d.width)
        .attr("height", d => d.height)
        .attr("fill", d => d.fillColor)
        .attr("stroke", d => d.strokeColor)
        .attr("stroke-width", 0)
        .on("mouseover", function(d) {
          objRef.hoverNode(d3.select(this));
        })
        .on("mouseout", function(d) {
          objRef.hoverNodeOut(d3.select(this));
        })
        .on("click", function() {
          event.stopPropagation();
          objRef.selectNode(d3.select(this));
        });

      this.nodeDepths = this.graph.nodes
        .map(n => objRef.data.nodes[n.id].sessionId)
        .reduce(this.reduceUnique, []);

      this.nodeDepths.forEach(d => {
        let nodesAtThisDepth = this.graph.nodes.filter(
          n => objRef.data.nodes[n.id].sessionId === d
        );
        let numberOfNodes = nodesAtThisDepth.length;
        let totalHeight = nodesAtThisDepth
          .map(n => n.height)
          .reduce(this.sumValues, 0);
        let whitespace = this.graphSize[1] - totalHeight;
        let balancedWhitespace = whitespace / (numberOfNodes + 1.0);
      });

      // Add hover effect to nodes.
      this.svgNodes
        .append("title")
        .text(d => this.templates.hover_node.format(d.id, d.value));

      // this.svgNodes.call(d3.drag()
      //                 .on("start", this.onDragStart)
      //                 .on("drag", this.onDragDragging)
      //                 .on("end", this.onDragEnd))
    },
    addGradientStop: function(gradients, offset, fn) {
      return this.gradients
        .append("stop")
        .attr("offset", offset)
        .attr("stop-color", fn);
    },
    color: function(index) {
      let ratio = index / (this.data.sankey.nodes.length - 1.0);
      return this.settings.colorScale(ratio);
    },
    getColors: function(indexes) {
      let objRef = this;
      let colorArray = [];

      indexes.forEach(d => {
        if (d.clusterId === undefined) {
          colorArray.push(undefined);
        } else {
          colorArray.push(objRef.color(d.clusterId));
        }
      });
      return colorArray;
    },
    darkenColor: function(color, factor) {
      return d3.color(color).darker(factor);
    },
    getGradientId: function(d) {
      return `gradient_${d.source.id}_${d.target.id}`;
    },
    getNodePosition: function(node) {
      return {
        x: +node.attr("x"),
        y: +node.attr("y"),
        width: +node.attr("width"),
        height: +node.attr("height")
      };
    },
    reduceUnique: function(previous, current) {
      if (previous.indexOf(current) < 0) {
        previous.push(current);
      }
      return previous;
    },
    sumValues: function(previous, current) {
      previous += current;
      return previous;
    },
    resizeHandler: function() {
      this.svg.remove();
      if (this.data) this.calculateSankey();
    },
    clearSelection: function() {
      if (!this.noSelection()) {
        this.settings.nodeSelection = false;
        this.settings.linkSelection = false;
        this.settings.sessionSelection = false;

        this.selectedLink = undefined;
        this.selectedNode = undefined;
        this.selectedSession = undefined;

        this.svgNodes.style("opacity", this.settings.opacity.active);

        this.svgLinks
          .select("path")
          .style("opacity", this.settings.opacity.active_link);

        // this.svg

        this.documents.forEach(d => (d.hidden = false));
      }
    },
    // NODE METHODS
    hoverNode: function(node) {
      let objRef = this;

      if (node !== undefined) {
        if (this.settings.nodeSelection) {
          if (this.selectedNode.datum().id != node.datum().id) {
            node.style("opacity", objRef.settings.opacity.active);
          }
        } else if (this.settings.linkSelection) {
          if (
            node.datum().id != this.selectedLink.datum().source.id ||
            node.datum().id != this.selectedLink.datum().target.id
          ) {
            node.style("opacity", objRef.settings.opacity.active);
          }
        } else if (this.settings.linkSelection) {
          if (node.datum().sessionId != this.selectedSession.id) {
            node.style("opacity", objRef.settings.opacity.active);
          }
        }
      }
    },
    hoverNodeOut: function(node) {
      let objRef = this;

      if (node !== undefined) {
        if (this.settings.nodeSelection) {
          if (this.selectedNode.datum().id != node.datum().id) {
            node.style("opacity", objRef.settings.opacity.faded);
          }
        } else if (this.settings.linkSelection) {
          if (
            node.datum().id != this.selectedLink.datum().source.id &&
            node.datum().id != this.selectedLink.datum().target.id
          ) {
            node.style("opacity", objRef.settings.opacity.faded);
          }
        } else if (this.settings.linkSelection) {
          if (node.datum().sessionId != this.selectedSession.id) {
            node.style("opacity", objRef.settings.opacity.faded);
          }
        }
      }
    },
    selectNode: function(node) {
      if (node !== undefined) {
        let objRef = this;
        this.settings.nodeSelection = true;
        this.settings.linkSelection = false;
        this.settings.sessionSelection = false;

        this.selectedNode = node;

        this.svgNodes.style("opacity", function(d) {
          return d.id == node.datum().id
            ? objRef.settings.opacity.selected
            : objRef.settings.opacity.faded;
        });

        this.svgLinks
          .select("path")
          .style("opacity", this.settings.opacity.faded);

        this.documents.forEach(function(d) {
          if (d.doc.nodesId.indexOf(node.datum().id) != -1) {
            d.hidden = false;
          } else {
            d.hidden = true;
          }
        });
      }
    },
    // LINK METHODS
    hoverLink: function(link) {
      let objRef = this;

      if (link !== undefined) {
        if (this.settings.linkSelection) {
          if (this.selectedLink.datum().id != link.datum().id) {
            d3.select(link.node()).style(
              "opacity",
              objRef.settings.opacity.active
            );
          }
        } else if (this.settings.nodeSelection) {
          d3.select(link.node()).style(
            "opacity",
            objRef.settings.opacity.active_link
          );
        } else if (this.settings.sessionSelection) {
          d3.select(link.node()).style(
            "opacity",
            objRef.settings.opacity.active_link
          );
        }
      }
    },
    hoverLinkOut: function(link) {
      let objRef = this;

      if (link !== undefined) {
        if (this.settings.linkSelection) {
          if (this.selectedLink.datum().id != link.datum().id) {
            d3.select(link.node()).style(
              "opacity",
              objRef.settings.opacity.faded
            );
          }
        } else if (this.settings.nodeSelection) {
          d3.select(link.node()).style(
            "opacity",
            objRef.settings.opacity.faded
          );
        } else if (this.settings.sessionSelection) {
          d3.select(link.node()).style(
            "opacity",
            objRef.settings.opacity.faded
          );
        }
      }
    },
    selectLink: function(link) {
      let objRef = this;
      if (link !== undefined) {
        let objRef = this;
        this.settings.linkSelection = true;
        this.settings.nodeSelection = false;
        this.settings.sessionSelection = false;

        this.selectedLink = link;

        this.svgLinks.select("path").style("opacity", function(d) {
          return d.id == link.datum().id
            ? objRef.settings.opacity.selected_link
            : objRef.settings.opacity.faded;
        });

        this.svgNodes.style("opacity", function(d) {
          if (d.id == link.datum().source.id || d.id == link.datum().target.id)
            return objRef.settings.opacity.selected;
          else return objRef.settings.opacity.faded;
        });

        this.documents.forEach(function(d) {
          if (
            d.doc.nodesId.indexOf(link.datum().source.id) != -1 &&
            d.doc.nodesId.indexOf(link.datum().target.id) != -1
          ) {
            d.hidden = false;
          } else {
            d.hidden = true;
          }
        });
      }
    },
    // DOCUMENT METHODS
    hoverDocument: function(doc) {
      let objRef = this;

      if (this.noSelection()) {
        this.svgNodes.style("opacity", function(d) {
          return doc.nodesId.indexOf(d.id) != -1
            ? objRef.settings.opacity.active
            : objRef.settings.opacity.faded;
        });

        this.svgLinks.select("path").style("opacity", function(d) {
          if (
            doc.nodesId.indexOf(d.source.id) != -1 &&
            doc.nodesId.indexOf(d.target.id) != -1
          ) {
            return objRef.settings.opacity.active_link;
          }
          return objRef.settings.opacity.faded;
        });
      }
    },
    hoverDocumentOut: function() {
      if (this.noSelection()) {
        this.svgNodes.style("opacity", this.settings.opacity.active);

        this.svgLinks
          .select("path")
          .style("opacity", this.settings.opacity.active_link);
      }
    },
    // SESSION METHODS
    selectSession: function(session) {
      if (session !== undefined) {
        let objRef = this;
        this.settings.nodeSelection = false;
        this.settings.linkSelection = false;
        this.settings.sessionSelection = true;

        this.selectedSession = session;

        this.svgNodes.style("opacity", function(d, i) {
          return objRef.data.nodes[i].sessionId == session.id
            ? objRef.settings.opacity.selected
            : objRef.settings.opacity.faded;
        });

        this.svgLinks
          .select("path")
          .style("opacity", this.settings.opacity.faded);

        this.documents.forEach(function(d) {
          if (d.doc.sessions[session.id] === undefined) {
            d.hidden = true;
          } else {
            d.hidden = false;
          }
        });
      }
    }
  }
};
</script>

<style lang="sass" scoped>

$bg-color: #292e33
$bg-color-secondary: #24282c
$font-color: #9a9a9a

::-webkit-scrollbar
    width: 5px
    -webkit-transition: width 5s

    &-track
        &-piece
            background: $bg-color

    &-thumb
        background: $bg-color-secondary
        &:hover
            background: #555

    // &, &-button, &-track-piece, &-thumb, &-corner, ::-webkit-resizer
        // background-color: $bg-color

#sankey
    // position: relative
    background-color: $bg-color
    width: 100%
    height: 100%
    g
        position: relative

#Spinner
	height: 100%
    display: flex
    align-items: center
    justify-content: center
	flex-direction: column

.wrap
    background-color: $bg-color
    color: $font-color
    width: 100%
    height: 100%
    margin: 0px
    border: 0px

.is-main-content
    padding: 10px

#sankeyToolbar
    height: 26px

    *
        padding: 0

    .sankeyToolbar_start
        text-align: left
        button
            width: 36px
            height: 26px
            cursor: pointer
            &:hover
                background: $bg-color-secondary

    .sankeyToolbar_end
        text-align: right
        button
            width: 36px
            height: 26px
            cursor: pointer
            &:hover
                background: $bg-color-secondary


.session
    line
        stroke: #000
    text.sessionLabel
        cursor: pointer
        text-anchor: middle
        fill: #FFF
        color: $font-color
        font-size: 20px
        line-height: 20px

svg
    align-self: center
    align-content: center
    width: 100%
    height: 100%

#rightPanel
    background: $bg-color
    box-shadow: 0 1px 50px $bg-color-secondary
    padding: 0 !important

#listHeader
    height: 10%
    text-align: center
    font-weight: normal
    padding: 5px
    font-size: 14px
    color: #eee
    background: rgba(41, 46, 51, .3)
    z-index: 1
    border-bottom: 0.5px solid $bg-color-secondary
    border-left: 0.5px solid $bg-color-secondary
    box-shadow: 0 0px 0px $bg-color-secondary
    -webkit-transition: border-bottom .3s, box-shadow .3s
    transition: border-bottom .3s, box-shadow .3s

    #listHeaderContent
        .row
            border: 0
            margin: 0
            align-items: center
            *
                width: 100%
        #title
            height: 70%
            span
                font-size: 14px
                line-height: 14px
                text-align: center

        #counter
            height: 30%
            width: 100%
            span
                font-size: 12px
                align-items: center
                text-align: center

    &.scroll
        border-bottom: 1px solid #999
        box-shadow: 0 1px 50px $bg-color-secondary

    #clear
      cursor: pointer
      opacity: 0.3
      .hidden
          display: none
      &:hover
          opacity: 1
      &:disabled
        opacity: 0.3
        &:hover
          opacity: 0.3

    #supp
        left: 0
        right: 0
        bottom: 17px
        font-size: 12px
        color: $font-color

.docs
    padding: 10px
    overflow-y: auto
    overflow-x: hidden
    scrollbar-color: $bg-color
    font-size: 12px
    text-align: left
    max-height: 10%
    -webkit-transition: display 1s
    // box-sizing: border-box
    // margin-left: 10px
    // margin-right: 10px
    // margin-bottom: 20px
    // margin-top: -5px
    .doc
        cursor: default
        padding-bottom: 2px
        color: $font-color
        font-size: 12px
        white-space: -moz-pre-wrap
        white-space: -pre-wrap
        white-space: -o-pre-wrap
        word-break: break-all
        word-wrap: break-word
        -webkit-transition: display 1s

        &.hidden
            -webkit-transition: display 1s
            display: none

        &:hover
            color: #eee

#footer
    font-size: 10px
    bottom: 7px
    margin-left: 45px
    opacity: .3
    -webkit-transition: opacity .3s
    transition: opacity .3s
    &:hover
        opacity: 1
    a
        color: #eee
        text-decoration: none
        &:after
            content: ''
            left: 0
            right: 0
            bottom: 0px
            height: 1px
            background: #9a9a9a
            opacity: 0
            -webkit-transition: opacity .3s
            transition: opacity .3s
        &:hover:after
            opacity: 1

</style>



